.gameContainer {
  max-width: 45rem;
  margin: 3rem auto;
  padding: 2rem;
  border-radius: 6px;
  background: #371c4b;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  position: relative;
}

.players {
  list-style: none;
  padding: 0;
  margin: 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.players li {
  display: flex;
  align-items: center;
  width: 50%;
  border: 2px solid transparent;
}

.players.highlightPlayer li.active {
  border-color: #f6e35a;
  animation: pulse 2s infinite ease-in-out;
}

.players.highlightPlayer li.active .playerName,
#players.highlightPlayer li.active .playerSymbol {
  color: #b9b7c7;
}

#players button {
  width: 3rem;
  border: none;
  background: none;
  color: #c3ba78;
  font-size: 0.9rem;
  cursor: pointer;
  transition: color 0.2s;
  padding: 0.5rem 0.25rem 0.25rem 0.25rem;
  text-align: center;
}

#players button:hover {
  color: #f8ca31;
}

.player {
  border: 2px solid transparent;
  padding: 0.5rem;
  border-radius: 4px;
  font-weight: bold;
}

.playerName {
  display: inline-block;
  width: 10rem;
  font-size: 1rem;
  color: #e1dec7;
  text-transform: uppercase;
  margin: 0;
  padding: 0.5rem;
  border-radius: 4px;
  text-overflow: ellipsis;
  text-align: center;
}

.player input {
  font: inherit;
  font-size: 1rem;
  width: 10rem;
  border: none;
  padding: 0.5rem;
  animation: pulse-text 2s infinite;
  background-color: #46432f;
  text-align: center;
  text-transform: uppercase;
}

.playerSymbol {
  margin-left: 1rem;
  margin-right: 1rem;
  font-size: 2.5rem;
  color: #e1dec7;
}

ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.preGame {
  text-align: center;
}

.preGame button {
  cursor: pointer;
  background: none;
  color: #f8c031;
  border: none;
  font-family: "Caprasimo", cursive;
  font-size: 4rem;
  text-shadow: 0 0 12px rgba(0, 0, 0, 0.7);
  animation: pulse-text-size 2s infinite ease-out;
}

.gameBoard {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 3rem 0;
  padding: 0;
  flex-direction: column;
}

.gameBoard ol {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0;
  padding: 0;
}

.gameBoard button {
  width: 8rem;
  border-radius: 7px;
  height: 8rem;
  border: none;
  background: #8e83aa;
  color: #371c4b;
  font-size: 5rem;
  cursor: pointer;
  font-family: "Caprasimo", cursive;
  padding: 1rem;
}

.editBtn{
  
  border: none;
  background: #371c4b;
  color: #8e83aa;
  font-size: 2.5rem;
  cursor: pointer;
  font-family: "Caprasimo", cursive;
  padding: 0.5rem;
  border-radius: 5px;
}

.gameOver {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #5A4573; 
  animation: pop-in 0.5s cubic-bezier(0.68, -0.55, 0.65, 0.52) forwards;
}

.gameOver h2 {
  font-family: "Caprasimo", cursive;
  font-size: 4rem;
  text-align: center;
  color: #e5e2f4;
  margin: 0;
}

.gameOver p {
  font-size: 2rem;
  text-align: center;
  color: #e1dec7;
}

.gameOver button {
  display: block;
  margin: 0 auto;
  font-size: 1.5rem;
  background: none;
  border: 2px solid #aa98b5;
  color: #aa98b5;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s, color 0.2s;
  box-shadow: 0 0 8px rgba(142, 146, 218, 0.4);
}

.gameOver button:hover {
  background: #aa98b5;;
  color: #2f1a3b;
  transform: scale(1.1);
  box-shadow: 0 0 20px rgba(169, 172, 226, 0.8);
}

.log {
  max-width: 20rem;
  color: #3f3b00;
  list-style: none;
  margin: 2rem auto;
  padding: 0;
  text-align: center;
}

.log li {
  border-radius: 4px;
  animation: slide-in-from-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
  margin: 0.75rem;
}

.log li.highlighted {
  background-color: #3f3b00;
  color: white;
}

.gameHints {
  text-align: center;
  color: #46432f;
}

.gameHints h2 {
  font-family: "Caprasimo", cursive;
  font-size: 2rem;
  margin: 0;
}

.gameHints ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.gameHints button {
  cursor: pointer;
  border: none;
  background: transparent;
  color: #23221f;
  font: inherit;
  margin-top: 1.5rem;
}

@keyframes slide-in-from-left {
  0% {
    opacity: 0;
    transform: translateX(-30%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes pulse-text {
  0% {
    color: #e1dec7;
  }
  50% {
    color: #9f9d83;
  }
  100% {
    color: #e1dec7;
  }
}

@keyframes pulse-text-size {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    border-color: #9979c0;
    box-shadow: 0 0 0 0 rgba(246, 227, 90, 0.4);
  }
  50% {
    border-color: #5A4573;
    box-shadow: 0 0 0 0.5rem rgba(248, 165, 49, 0);
  }
  100% {
    border-color: #7357944d;
    box-shadow: 0 0 0 0 rgba(246, 227, 90, 0);
  }
}

/* Fancy animation for showing the "Game Over" element */
@keyframes pop-in {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  80% {
    transform: scale(1.1);
    opacity: 1;
  }
  100% {
    transform: scale(1);
  }
}

